@charset "UTF-8";
/* stylelint-disable at-rule-empty-line-before */
/* simple clearfix */
/***********************************************************************
 ***********************************************************************
 ***********************************************************************

## Component - Base

コンポーネント設定。  
各ページでよく使い回す記述。  
このサイト専用の共通設定。
*/
.category_line::before {
  height: 200%;
}
@media screen and (max-width: 575px) {
  .category_line::before {
    height: 150%;
  }
}
.category_line .text_box p {
  padding-left: 0;
  margin-bottom: 0;
}

.red_text {
  color: #e76c80;
  font-weight: normal;
}

.required {
  color: #ffffff;
  margin-left: 0.7em;
  font-size: 0.813rem;
  font-weight: bold;
  border-radius: 20px;
  background-color: #ed8767;
  width: 50px;
  height: 23px;
  display: inline-block;
  text-align: center;
  align-content: center;
}
.required-ml0 {
  margin-left: 0;
}
@media screen and (min-width: 1000px), print {
  .required {
    margin-top: 4px;
  }
}

.red_label {
  color: #cd5d3b;
  margin-left: 0.5em;
  line-height: 26px;
  height: 26px;
  box-sizing: border-box;
  display: inline-block;
}

.form_container {
  animation-name: fade-in;
  animation-duration: 0.2s;
  animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  animation-fill-mode: forwards;
}

.contact {
  background-color: #ffffff;
}
.contact input,
.contact textarea,
.contact select,
.contact option {
  box-sizing: border-box;
  font-size: 18px;
  width: 100%;
}
.contact input,
.contact select {
  font-weight: 600;
}
.contact input ::placeholder,
.contact select ::placeholder {
  color: #9fa0a0;
}
.contact textarea {
  min-height: 200px;
}
.contact textarea ::placeholder {
  color: #9fa0a0;
}
@media screen and (min-width: 1000px), print {
  .contact .textarea_frame textarea {
    height: 250px;
  }
}
@media screen and (max-width: 999px) {
  .contact .textarea_frame textarea {
    height: 200px;
  }
}
.contact__body {
  width: 100%;
  margin: 0 auto;
  box-sizing: border-box;
}
@media screen and (max-width: 767px) {
  .contact__body {
    margin: 0 auto 40px;
    padding-bottom: 15px;
  }
}
@media screen and (max-width: 575px) {
  .contact__body {
    padding-left: 5px;
    padding-right: 5px;
  }
}
@media screen and (min-width: 1000px), print {
  .contact__gr {
    display: flex;
    padding-bottom: 30px;
  }
}
@media screen and (max-width: 999px) {
  .contact__gr {
    padding-bottom: 2em;
  }
}
.contact__gr-submit {
  position: relative;
  top: 0;
  left: 0;
}
@media screen and (min-width: 1000px), print {
  .contact__gr-submit {
    padding-top: 45px;
  }
}
.contact__gr-submit .link_arrow-prev {
  position: absolute;
  top: 65px;
  left: 0;
}
.contact__gr input[type=radio],
.contact__gr input[type=checkbox] {
  margin-right: 0.6em;
}
.contact__gr input[type=date i]::-webkit-calendar-picker-indicator {
  cursor: pointer;
}
.contact__gr .form_btn_wrap {
  margin: 30px auto 0;
  width: 100%;
}
.contact__gr._submit {
  padding-top: 20px;
  padding-bottom: 0;
}
@media screen and (max-width: 575px) {
  .contact__gr._submit {
    padding-top: 20px;
  }
}
.contact__th {
  width: clamp(200px, 25vw, 280px);
  flex-shrink: 0;
  box-sizing: border-box;
  letter-spacing: 0.02em;
  color: #000000;
  font-size: 1.125rem;
}
@media screen and (min-width: 1000px), print {
  .contact__th {
    min-width: 280px;
    display: flex;
    margin-top: 10px;
  }
}
@media screen and (max-width: 999px) {
  .contact__th {
    width: 100%;
    margin-bottom: 10px;
    margin-left: 0.2rem;
  }
}
.contact__td {
  width: 100%;
  position: relative;
  top: 0;
  left: 0;
}
.contact__td p {
  margin-bottom: 0;
  padding-left: 0;
}
.contact__td.name {
  display: grid;
  grid-template-columns: 1fr 1fr;
  column-gap: 15px;
}
@media screen and (max-width: 575px) {
  .contact__td.name {
    column-gap: 8px;
  }
}
.contact__td._medium input {
  width: 100%;
  max-width: 240px;
}
.contact__td._medium select {
  width: 240px;
}
.contact__td._short input {
  width: 100%;
  max-width: 167px;
}
@media screen and (max-width: 999px) {
  .contact__td._short input {
    max-width: 175px;
  }
}
.contact__td._short select {
  width: 170px;
}
.contact__td._row {
  display: flex;
}
.contact__td._row .pre_text,
.contact__td._row .rear_text {
  display: inline-block;
  margin-top: 0.4rem;
}
.contact__td._row .pre_text {
  margin-right: 1rem;
}
.contact__td._row .rear_text {
  margin-left: 1rem;
}
.contact__td select,
.contact__td input,
.contact__td textarea {
  width: 100%;
  box-sizing: border-box;
  padding: 16px 16px 14px;
  border-radius: 3px;
  outline: none;
  border: none;
  background-color: #f8f8f8;
  color: #000000;
}
.contact__td select {
  padding: 8px 25px 8px 18px;
  appearance: none;
  cursor: pointer;
}
.form-confirm .contact__td {
  word-break: break-all;
}
.contact__td .text_frame-unit input {
  margin-right: 0.5em;
}
@media screen and (min-width: 1000px), print {
  .contact__td .text_frame-unit input {
    width: 93%;
  }
}
@media screen and (max-width: 999px) {
  .contact__td .text_frame-unit input {
    width: 90%;
  }
}
.contact__td .text_frame-sm input {
  max-width: 7em;
}
.contact__td .text_frame-md input {
  max-width: 15em;
}
.contact__td .text_frame-tel input {
  width: 5em;
}
.contact__td .text_frame .unit {
  color: #222222;
}
@media screen and (min-width: 1000px), print {
  .contact__td .text_frame .unit {
    font-size: 16px;
  }
}
.contact__td .time_frame {
  margin-top: 0.5em;
}
.contact__td .time_frame .cell {
  margin-left: 0.2em;
  margin-right: 0.2em;
}
.contact__td .time_frame select {
  width: 4em;
}
.contact__note {
  font-size: 1rem;
  color: #9fa0a0;
}
.contact__privacy {
  width: 100%;
  max-width: 585px;
  border: 5px solid #f8f8f8;
  padding: 30px;
  margin: 20px auto 0;
  box-sizing: border-box;
}
.contact__privacy p {
  line-height: 1.7;
  letter-spacing: 0.08em;
}
.contact__privacy .agreement {
  display: flex;
  align-items: center;
  justify-content: center;
  /* カスタムチェックボックスのスタイル */
}
.contact__privacy .agreement input[type=checkbox] {
  width: 20px;
  margin-top: 4px;
}
.contact__privacy .agreement label {
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative;
  padding-left: 12px;
}

/**************************
	各項目パーツ
*/
.text_frame input {
  width: 100%;
  font-size: 0.938rem;
  box-sizing: border-box;
  margin: 0;
  height: 50px;
  padding: 0 0.7em;
  padding-right: 0.5em;
  background-color: rgb(248, 248, 248);
  box-shadow: inset 2.121px 2.121px 5px 0 rgba(210, 210, 210, 0.2);
}
.text_frame input:focus, .text_frame input:active {
  outline: 2px solid #a6a6a6;
  outline-offset: -2px;
}
.text_frame .cell {
  display: inline-block;
  margin-left: 0.5em;
  margin-right: 0.5em;
}
.text_frame-zip .cell {
  display: inline-block;
  margin-right: 0.5em;
}
.text_frame-zip input {
  width: 6em;
  margin-right: 0;
}

.select_frame {
  min-height: 40px;
  position: relative;
  top: 0;
  left: 0;
}
.select_frame::after {
  display: block;
  content: "";
  width: 9px;
  height: 8px;
  background: url("../img/icon/arrow/sankaku_black.webp") no-repeat;
  background-size: contain;
  position: absolute;
  top: 1rem;
  right: 16px;
  z-index: 2;
}
.select_frame select {
  -webkit-user-modify: read-write-plaintext-only;
  -moz-user-modify: read-write-plaintext-only;
  box-sizing: border-box;
  margin: 0;
  border: 0 none;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 40px;
  cursor: pointer;
  background-color: #f8f8f8;
}
.select_frame select:focus + .select_frame__display, .select_frame select:active + .select_frame__display {
  outline: 2px solid #a6a6a6;
  outline-offset: -2px;
}
.select_frame option {
  padding: 0 15px;
}
.select_frame._pref,
.select_frame._pref .select_frame__display,
.select_frame._pref select {
  width: 6em;
}

.select_area .date_box {
  display: flex;
}
@media screen and (max-width: 575px) {
  .select_area .date_box {
    flex-wrap: wrap;
  }
}
.select_area .date_box .date {
  box-sizing: border-box;
  display: flex;
  align-items: center;
}
.select_area .date_box .date._year {
  width: 36%;
  max-width: 180px;
}
@media screen and (max-width: 575px) {
  .select_area .date_box .date._year {
    width: 100%;
    max-width: none;
    margin-bottom: 1rem;
  }
}
.select_area .date_box .date._year .select_frame {
  width: 120px;
}
@media screen and (max-width: 575px) {
  .select_area .date_box .date._year .select_frame {
    width: 260px;
  }
}
.select_area .date_box .date._month, .select_area .date_box .date._day {
  width: 32%;
  max-width: 160px;
}
@media screen and (max-width: 575px) {
  .select_area .date_box .date._month, .select_area .date_box .date._day {
    width: 50%;
  }
}
.select_area .date_box .date._month .select_frame, .select_area .date_box .date._day .select_frame {
  width: 100px;
}
.select_area .date_box .date .date_text {
  margin-left: 11px;
  font-size: 1.125rem;
}
@media screen and (max-width: 575px) {
  .select_area .date_box .date .date_text {
    font-size: 1rem;
  }
}

.textarea_frame textarea {
  width: 100%;
  font-size: 1rem;
  box-sizing: border-box;
  line-height: 1.5em;
  margin: 0;
  border: 1px solid #cccccc;
  padding: 1em 1.5em;
  height: 6em;
  resize: none;
  background-color: rgb(248, 248, 248);
  box-shadow: inset 2.121px 2.121px 5px 0 rgba(210, 210, 210, 0.2);
}
.textarea_frame textarea:focus, .textarea_frame textarea:active {
  outline: 2px solid #a6a6a6;
  outline-offset: -2px;
}
.textarea_frame-m textarea {
  height: 5em;
}

.radio_frame {
  position: relative;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.radio_frame label {
  margin-right: 2rem;
}
.radio_frame input[type=radio] {
  box-shadow: inset 2.121px 2.121px 5px 0 rgba(210, 210, 210, 0.2);
  appearance: none;
  width: 18px;
  height: 18px;
  border-radius: 50%;
  padding: 0;
  transform: translateY(0.2rem);
}
.radio_frame input[type=radio]:checked {
  background-color: #4fb4d9;
  border: double 4px white;
}

.check_frame {
  position: relative;
  top: 0;
  left: 0;
  min-width: 19px;
  min-height: 19px;
}
.check_frame-focus .check_frame__display {
  outline: 2px solid #a6a6a6;
}
.check_frame__icon {
  position: absolute;
  top: 0;
  left: 0;
  opacity: 0;
}
.check_frame__display {
  display: block;
  box-sizing: border-box;
  position: absolute;
  left: 0;
}
.check_frame__input {
  cursor: pointer;
  position: absolute;
  -webkit-user-modify: read-write-plaintext-only;
  -moz-user-modify: read-write-plaintext-only;
  z-index: 1;
  left: 0;
  width: 19px;
  height: 19px;
  box-sizing: border-box;
  opacity: 0;
  padding: 0;
  margin: 0;
  border: 0 none;
}
.check_frame__input:focus + .check_frame__display {
  outline: 2px solid #a6a6a6;
  outline-offset: -2px;
}
.check_frame__display, .check_frame__input {
  top: 0.25em;
}
.check_frame__label {
  margin-left: 24px;
  display: block;
}
.check_frame__input:checked + .check_frame__display .check_frame__icon {
  opacity: 1;
  transform: scale(1);
}

.form_btn_wrap .wpcf7-previous {
  margin-top: 15px;
  width: auto;
  border: none;
  background-color: transparent;
  box-shadow: none;
  text-decoration: underline;
  font-size: 1rem;
  letter-spacing: 0.14em;
}

.btn_box._submit_btn {
  position: relative;
  background-color: #f49494;
  mix-blend-mode: multiply;
  width: 310px;
  height: 70px;
  margin: 0 auto;
  align-content: center;
  box-sizing: border-box;
  border-radius: 50px;
  font-weight: bold;
  text-align: center;
}
@media screen and (max-width: 767px) {
  .btn_box._submit_btn {
    width: 280px;
    height: 100px;
  }
}
@media screen and (max-width: 420px) {
  .btn_box._submit_btn {
    width: 220px;
    height: 60px;
  }
}
.btn_box._submit_btn p {
  padding: 0;
  margin: 0;
}
.btn_box._submit_btn input[type=submit] {
  font-size: 1.125rem;
  font-weight: bold;
  letter-spacing: 0.12em;
  appearance: none;
  outline: none;
  border: none;
  background-color: transparent;
  color: #ffffff;
  box-shadow: none;
  z-index: 2;
  align-content: center;
  cursor: pointer;
}
@media screen and (max-width: 767px) {
  .btn_box._submit_btn input[type=submit] {
    width: 280px;
    height: 100px;
  }
}
@media screen and (max-width: 420px) {
  .btn_box._submit_btn input[type=submit] {
    width: 220px;
    height: 60px;
  }
}
.btn_box._submit_btn::after {
  content: "";
  background: url("../img/icon/arrow_white.webp") no-repeat;
  background-size: 33px 14px;
  width: 17px;
  height: 14px;
  position: absolute;
  inset: auto;
  top: 50%;
  right: 20px;
  transform: translateY(-50%);
}
@media screen and (max-width: 420px) {
  .btn_box._submit_btn::after {
    top: 47%;
    right: 40px;
  }
}
.btn_box._submit_btn:hover {
  cursor: pointer;
}

.submit_btn {
  border: none;
  outline: none;
  margin: 30px auto 0;
  display: block;
  overflow: hidden;
  width: 240px;
  position: relative;
}
.submit_btn::before {
  content: "";
  display: block;
  width: 10px;
  min-width: 10px;
  height: 11px;
  background: url("../img/icon/arrow/btn_arrow.webp") no-repeat;
  background-size: contain;
  background-position: 0 0;
  transition: transform 0.1s linear;
  position: absolute;
  inset: 24px auto auto 50px;
  margin: auto;
  z-index: 2;
}
.submit_btn::after {
  content: "";
  display: inline-block;
}
.submit_btn > .btn {
  width: 240px;
  height: 60px;
  min-height: 60px;
  border: none;
  border-radius: 30px;
}
.submit_btn .back_btn {
  outline: 0;
  display: inline-block;
  margin: 0 0 0 auto;
  border: none;
  background-color: transparent;
  text-decoration: underline;
  text-align: center;
  transition: 0.2s;
}
.submit_btn .back_btn:hover {
  color: #23beb8;
}

/**************************
	フローバー 共通
*/
.contact__flow-list {
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 30px auto 40px;
  position: relative;
  font-weight: 600;
}
.contact__flow-list > li {
  text-align: center;
}
.contact__flow-list > li.line {
  width: clamp(50px, 10vw, 100px);
  height: 1px;
  position: relative;
  top: -14px;
  background-image: linear-gradient(to right, transparent 0, #c4c4c4 2px);
}
.contact__flow-list > li .info {
  width: 38px;
  height: 38px;
  border: solid 1px #ed8767;
  border-radius: 50%;
  align-content: center;
  letter-spacing: 0.08em;
  text-align: center;
  position: relative;
}
@media screen and (max-width: 575px) {
  .contact__flow-list > li .info {
    width: 60px;
    height: 60px;
  }
}
.contact__flow-list > li .info .text {
  color: #ed8767;
  font-size: 1rem;
}
.contact__flow-list > li.active .info {
  background-color: #ed8767;
}
.contact__flow-list > li.active .info .text {
  color: #ffffff;
  font-size: 1rem;
}
.contact__flow-list > li.active .info span {
  margin-top: 14px;
  display: block;
  color: #111111;
}
.contact__flow-list span {
  margin-top: 8px;
  display: block;
  color: #9fa0a0;
  font-size: 14px;
}
.contact__flow-list .active span {
  color: #111111;
}

/**************************
	入力画面
*/
/**************************
	確認画面
*/
.contact._confirm {
  margin-top: 60px;
}
.contact._confirm .contact__th {
  margin-left: 0;
}
.contact._confirm .contact__td {
  font-size: 1.125rem;
}
.contact._confirm .contact__td input {
  padding: 16px 20px;
  min-height: 30px;
  width: 100%;
}
.contact._confirm .select_area .date_box .date._year .select_frame,
.contact._confirm .select_area .date_box .date._month .select_frame,
.contact._confirm .select_area .date_box .date._day .select_frame {
  width: auto;
}
.contact._confirm .select_frame {
  align-content: center;
  width: auto;
}
.contact._confirm .select_frame::after {
  content: none;
  display: none;
}
.contact._confirm .pre_text,
.contact._confirm .rear_text {
  margin-top: 0;
  align-content: center;
}

/**************************
	完了画面
*/
textarea::placeholder {
  color: #9fa0a0;
}